"use client"

import { useEffect, useRef, useState } from "react"
import { SectionBadge } from "@/components/section-badge"

const capabilities = [
  {
    title: "Digital Modernization & Platform Redesign",
    description:
      "Transforming outdated digital experiences into modern, performant platforms that reflect your evolved standards.",
  },
  {
    title: "UX/UI & Experience Strategy",
    description:
      "Crafting intuitive interfaces grounded in user research, strategic thinking, and aesthetic precision.",
  },
  {
    title: "Custom Digital Solutions for Events",
    description: "Building bespoke event platforms that elevate attendee experiences and streamline operations.",
  },
  {
    title: "Bespoke Websites for Agencies & Event Planners",
    description: "Creating sophisticated digital presences that showcase your work and win discerning clients.",
  },
  {
    title: "Performance, Accessibility & Scalability",
    description: "Engineering solutions that load fast, work for everyone, and grow with your ambitions.",
  },
]

export function CapabilitiesSection() {
  const [visibleItems, setVisibleItems] = useState<number[]>([])
  const sectionRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            capabilities.forEach((_, index) => {
              setTimeout(() => {
                setVisibleItems((prev) => [...prev, index])
              }, index * 100)
            })
            observer.disconnect()
          }
        })
      },
      { threshold: 0.1 },
    )

    if (sectionRef.current) {
      observer.observe(sectionRef.current)
    }

    return () => observer.disconnect()
  }, [])

  return (
    <section id="capabilities" ref={sectionRef} className="py-24 md:py-32 lg:py-48 px-6 md:px-12 lg:px-20 border-t border-border">
      <div className="max-w-7xl mx-auto">
        <SectionBadge number={1} label="Capabilities" />
        <h2 className="text-5xl md:text-6xl lg:text-7xl xl:text-8xl font-normal text-foreground mb-24 md:mb-32 tracking-tight leading-[0.9]">
          Capabilities
        </h2>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-16 md:gap-20 lg:gap-24">
          {capabilities.map((capability, index) => (
            <div
              key={index}
              className={`transition-all duration-700 ${
                visibleItems.includes(index) ? "opacity-100 translate-y-0" : "opacity-0 translate-y-4"
              }`}
            >
              <h3 className="text-2xl md:text-3xl lg:text-4xl font-normal text-foreground mb-6 leading-tight tracking-tight">
                {capability.title}
              </h3>
              <p className="text-lg md:text-xl text-muted-foreground leading-relaxed font-normal">
                {capability.description}
              </p>
            </div>
          ))}
        </div>
      </div>
    </section>
  )
}
